.chatWindow {
    position: fixed;
    min-height: 650px;
    height: 96%;
    width: 450px;
    right: 3px;
    background-color: #F6F8FB;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 12px 24px 0px;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
}
.chatWidget{
    position: fixed;
    font-size: 40px;
    padding: 10px;
    width: 60x;
    height: 60px;
    bottom: 40px;
    right: 40px;
    background-color: #0C9;
    color: #FFF;
    border-radius: 50px;
    text-align: center;
    box-shadow: 2px 2px 3px #999;
}
.chatWidget:hover {
    background-color: #008060;     
}
.cuteBot{
    position: fixed;
    padding: 10px;
    bottom: 40px;
    right: 30px;
    color: #FFF;
    height: 110px; 
    width: auto;
}

.inputField {
    font-size: 17px;
    height: 58px;
    padding-left: 5%;
    padding-right: 12%;
    width: 100.1%;
    border-top: 0.5px solid #CDCDCD;
    background: #FFFFFF;
    border-left: none;
    border-right: none;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;  
    position: absolute;  
}
.inputField:focus {
    outline: none;
    border-bottom: 2px solid #0C9; 
    transition: ease-out;
}   
.arrowInput{
    font-size: 25px;
    float: right;
    position: relative;
    margin-right: 13px;
    margin-top: 16px;
    z-index: 1;
}
.arrowInput:hover {
    color: #0C9;
    cursor: pointer;
}
.hideIcon {
    float: right;
}
.hideIcon:hover{
    color: #595959;
}
.botHeader {
    font-weight: bold;
    font-size: 30px;
    background-color: #0C9;
    height: 60px;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    text-align: center;
    padding: 10px;
    color: #fff;
    border-radius: 70%/0 0 5px 90px;
}
.messageArea{
    min-Height: 85%;
    max-Height: 85%;
    width: 100%;
    overflow: auto;
}
@media only screen and (max-width: 500px) {
  .chatWindow {
    width: 100%;
    height: 92%;
    bottom: 52px;
    right: 0;
 }
 .messageArea{
    min-Height: 90%;
    max-Height: 85%;
    width: 100%;
    overflow: auto;
}
.inputField {
    height: 58px;
    margin-bottom: 10px;
  }
}

